
import Nav from '../common/user_nav'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
components: {
Nav,
Group,
Cell,
XHeader,
Icon,
Loading
},
data() {
return {
healths: [],
user_id: '',
loadings:true
}
},
methods: {
getHealthHistory() {
axios.post('api/health/userList', {user_id: this.$route.params.user_id}).then(response => {
this.healths = response.data.data
this.loadings = false
console.log(this.healths)
}).catch(error => {
this.loadings = false
})
},
toHealthDetail(id) {
this.$router.push('/health_detail/' + id)
}
},
mounted() {
this.getHealthHistory()
}
}



